@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
     ========================================================================== */
/**
   * Remove the margin in all browsers.
   */
body {
  margin: 0;
}

/**
   * Render the `main` element consistently in IE.
   */
main {
  display: block;
}

/**
   * Correct the font size and margin on `h1` elements within `section` and
   * `article` contexts in Chrome, Firefox, and Safari.
   */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
     ========================================================================== */
/**
   * 1. Add the correct box sizing in Firefox.
   * 2. Show the overflow in Edge and IE.
   */
hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}

/**
   * 1. Correct the inheritance and scaling of font size in all browsers.
   * 2. Correct the odd `em` font sizing in all browsers.
   */
pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
     ========================================================================== */
/**
   * Remove the gray background on active links in IE 10.
   */
a {
  background-color: transparent;
}

/**
   * 1. Remove the bottom border in Chrome 57-
   * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
   */
abbr[title] {
  border-bottom: none; /* 1 */
  text-decoration: underline; /* 2 */
  text-decoration: underline dotted; /* 2 */
}

/**
   * Add the correct font weight in Chrome, Edge, and Safari.
   */
b,
strong {
  font-weight: bolder;
}

/**
   * 1. Correct the inheritance and scaling of font size in all browsers.
   * 2. Correct the odd `em` font sizing in all browsers.
   */
code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
   * Add the correct font size in all browsers.
   */
small {
  font-size: 80%;
}

/**
   * Prevent `sub` and `sup` elements from affecting the line height in
   * all browsers.
   */
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* Embedded content
     ========================================================================== */
/**
   * Remove the border on images inside links in IE 10.
   */
img {
  border-style: none;
}

/* Forms
     ========================================================================== */
/**
   * 1. Change the font styles in all browsers.
   * 2. Remove the margin in Firefox and Safari.
   */
button,
input,
optgroup,
select,
textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}

/**
   * Show the overflow in IE.
   * 1. Show the overflow in Edge.
   */
button,
input {
  /* 1 */
  overflow: visible;
}

/**
   * Remove the inheritance of text transform in Edge, Firefox, and IE.
   * 1. Remove the inheritance of text transform in Firefox.
   */
button,
select {
  /* 1 */
  text-transform: none;
}

/**
   * Correct the inability to style clickable types in iOS and Safari.
   */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
   * Remove the inner border and padding in Firefox.
   */
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
   * Restore the focus styles unset by the previous rule.
   */
button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
   * Correct the padding in Firefox.
   */
fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
   * 1. Correct the text wrapping in Edge and IE.
   * 2. Correct the color inheritance from `fieldset` elements in IE.
   * 3. Remove the padding so developers are not caught out when they zero out
   *    `fieldset` elements in all browsers.
   */
legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  padding: 0; /* 3 */
  white-space: normal; /* 1 */
}

/**
   * Add the correct vertical alignment in Chrome, Firefox, and Opera.
   */
progress {
  vertical-align: baseline;
}

/**
   * Remove the default vertical scrollbar in IE 10+.
   */
textarea {
  overflow: auto;
}

/**
   * 1. Add the correct box sizing in IE 10.
   * 2. Remove the padding in IE 10.
   */
[type=checkbox],
[type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}

/**
   * Correct the cursor style of increment and decrement buttons in Chrome.
   */
[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

/**
   * 1. Correct the odd appearance in Chrome and Safari.
   * 2. Correct the outline style in Safari.
   */
[type=search] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
   * Remove the inner padding in Chrome and Safari on macOS.
   */
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
   * 1. Correct the inability to style clickable types in iOS and Safari.
   * 2. Change font properties to `inherit` in Safari.
   */
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/* Interactive
     ========================================================================== */
/*
   * Add the correct display in Edge, IE 10+, and Firefox.
   */
details {
  display: block;
}

/*
   * Add the correct display in all browsers.
   */
summary {
  display: list-item;
}

/* Misc
     ========================================================================== */
/**
   * Add the correct display in IE 10+.
   */
template {
  display: none;
}

/**
   * Add the correct display in IE 10.
   */
[hidden] {
  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.header {
  background-image: url(../../../build/img/header.webp);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 55rem;
  display: flex;
  justify-content: center;
}
.header .container-text {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1.5rem;
}
.header .container-text h3 {
  color: #ffffff;
  font-weight: 300;
}

.footer {
  background-color: #333333;
  padding: 1.8rem;
}
.footer .container__footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.5rem;
}
@media (width < 480px) {
  .footer .container__footer {
    gap: 3rem;
  }
}
.footer .container__footer h2 {
  width: 100%;
  color: #ffffff;
  font-weight: 500;
  text-transform: uppercase;
  font-size: clamp(1.7rem, 4.5vw, 2.2rem);
}
.footer .container__footer h2 span {
  font-weight: 700;
}
.footer .container__footer .links_footer {
  display: flex;
  gap: 1.2rem;
}
@media (width < 480px) {
  .footer .container__footer .links_footer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
  }
}
.footer .container__footer .links_footer a {
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  margin: 0 1rem;
}

.header .container-header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
}
.header .container-header .nav {
  padding: 1rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
@media (width < 768px) {
  .header .container-header .nav {
    justify-content: space-between;
  }
}
.header .container-header .nav > h2 {
  color: #ffffff;
  font-weight: 300;
  text-transform: uppercase;
  font-size: 3.1rem;
}
.header .container-header .nav > h2 span {
  font-weight: 700;
}
.header .container-header .nav .container__checkbox {
  display: none;
}
@media (width < 768px) {
  .header .container-header .nav .container__checkbox {
    display: block;
  }
}
.header .container-header .nav .container__checkbox #checkbox {
  display: none;
}
.header .container-header .nav .container__checkbox .toggle {
  position: relative;
  z-index: 20;
  width: 30px;
  height: 30px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  transition-duration: 0.5s;
}
.header .container-header .nav .container__checkbox .toggle .bars {
  width: 100%;
  height: 4px;
  background-color: #ffffff;
  border-radius: 3px;
}
.header .container-header .nav .container__checkbox .toggle #bar2 {
  transition-duration: 0.8s;
}
.header .container-header .nav .container__checkbox .toggle #bar1,
.header .container-header .nav .container__checkbox .toggle #bar3 {
  width: 70%;
}
.header .container-header .nav .container__checkbox #checkbox:checked + .toggle .bars {
  position: absolute;
  transition-duration: 0.5s;
}
.header .container-header .nav .container__checkbox #checkbox:checked + .toggle #bar2 {
  transform: scaleX(0);
  transition-duration: 0.5s;
}
.header .container-header .nav .container__checkbox #checkbox:checked + .toggle #bar1 {
  width: 100%;
  transform: rotate(45deg);
  transition-duration: 0.5s;
}
.header .container-header .nav .container__checkbox #checkbox:checked + .toggle #bar3 {
  width: 100%;
  transform: rotate(-45deg);
  transition-duration: 0.5s;
}
.header .container-header .nav .container__checkbox #checkbox:checked + .toggle {
  transition-duration: 0.5s;
  transform: rotate(180deg);
}
.header .container-header .nav .nav__links {
  display: flex;
  align-items: center;
  gap: 1.8rem;
}
@media (width < 768px) {
  .header .container-header .nav .nav__links {
    position: fixed;
    height: 100dvh;
    width: 50%;
    flex-direction: column;
    justify-content: center;
    gap: 3.2rem;
    font-weight: 500;
    top: 0;
    right: 0;
    background-color: #272727;
    transform: translateX(100%);
    transition: all 0.3s ease-in-out;
  }
}
.header .container-header .nav .nav__links .nav__link {
  text-decoration: none;
  color: #ffffff;
}